body {
  margin: 0;
  padding: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(#000000, rgb(94, 94, 94), rgb(0, 0, 0));
  font-family: Arial, sans-serif;
  max-width: 100%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;  /* Szerokość na 100% widocznej szerokości ekranu */
  height: 100vh; /* Wysokość na 100% widocznej wysokości ekranu */
  text-align: center;
  border: none; /* Usunięcie obramowania */
  border-radius: 0px; /* Usunięcie zaokrąglonych rogów */
  padding: 0; /* Usunięcie wewnętrznego odstępu */
  background-image: url('./bg_photo.jpg');
  background-size: cover;
  background-position: center; /* Centrum tła */
  box-shadow: none; /* Usunięcie cienia */
}

.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
}

.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid #ddd;
  object-fit: cover;
}

.profile-name {
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0.7);
  margin-top: 10px;
  font-size: 29px;
  padding: 3px;
  padding-top: 0px;
  font-weight: bolder;
  color: #000000;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #fff;
}

.icon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
 
  
}

.icon-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.icon {
  align-items: center;
  justify-content: center;
  width: 80%; /* Ikony zajmują 80% szerokości kontenera */
  max-width: 500px; /* Maksymalna szerokość 500px */
  font-size: 50px;
  text-decoration: none;
  padding: 10px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  transition: background-color 0.3s, color 0.3s;
}

.icon span {
  margin-left: 10px;
  font-size: 20px;
  color: #fff;
}

.icon.instagram {
  width: 200px;
  background-color: rgba(228, 64, 95, 0.5);
  color: #fff;
  font-weight: bold;
}

.icon.instagram:hover {
  background-color: rgba(228, 64, 95, 1);
  color: #fff;
}

.icon.snapchat {
  width: 200px;
  background-color: rgba(255, 252, 0, 0.5);
  color: #000;
  font-weight: bold;
}

.icon.snapchat:hover {
  background-color: rgba(255, 252, 0, 1);
  color: #000;
}

.icon.spotify{
  width: 200px;
  background-color: rgb(31, 30, 30, 0.8);
  color: #1ED760;
  font-weight: bold;
}
.icon.spotify:hover{
  background-color: #000000;
}
.icon.phone {
  width: 200px;
  background-color: rgba(37, 211, 102, 0.5);
  color: #fff;
  font-weight: bold;
}

.icon.phone:hover {
  background-color: rgba(37, 211, 102, 1);
  color: #fff;
}


/* Media queries dla mniejszych ekranów */
@media (max-width: 768px) {
  .profile-pic {
      width: 120px;
      height: 120px;
  }

  .profile-name {
      font-size: 24px;
  }

  .icon {
      font-size: 40px;
      padding: 8px;
  }

  .icon span {
      font-size: 18px;
  }
}

@media (max-width: 480px) {
  .profile-pic {
      width: 100px;
      height: 100px;
  }

  .profile-name {
      font-size: 20px;
  }

  .icon {
      font-size: 35px;
      padding: 6px;
  }

  .icon span {
      font-size: 16px;
  }
}
